.header{
    position: absolute;
    display: flex;
    flex-direction: column;
    width: 100%;
    text-align: center;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    font-size: 5vw;
    color: white;
    z-index: 2;
    font-weight: bold
}
.teamLogos_wrapper{
    display: flex;
    flex-direction: column;
    width: 100%;
    align-items: center;
}
.teamLogos_wrapper_header{
    color: white;
    font-size: 4vw;
    margin-bottom: 50px;
}
.show_players_div{
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    left: 39%;
    span{
        font-size: 1.5vw;
        color: white;
        font-weight: bold;

    }   
}
.teamLogos{
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    gap: 10px;
    width: 100%;
}
.logo{
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 20px;
    width: 23%;
    height: 200px;
    background: rgba(255, 255, 255, 0.1);
    border-radius: 5px;
    img{
        width: 100px;
        height: 100px
    }
    .players_name{
        display: none;
    }
}

.logo1{
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 20px;
    width: 23%;
    height: 200px;
    background: rgba(255, 255, 255, 0.1);
    border-radius: 5px;

    img{
        position: absolute;
        width: 50%;
        height: auto;
        opacity: 0.2;
    }
    span{
        display: none;
    }
    .players_name{
        width: 90%;
        display: flex;
        align-items: center;
        flex-direction: column;
        color: white;
        gap: 5px;
        z-index: 1;
        div{
            display: flex;
            align-items: center;
            width: 65%;
            gap: 10px;
            img{
                position: absolute;
                width: 20px;
                opacity: 1;
            }
            span{
                display: inline;
                font-size: 16px;
                margin-left: 30px;
                
            }
        }
        
    }
}

.custom-button {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 70%;
    height: 50px;
    border-radius: 25px;
    background: linear-gradient(135deg, #FFD700, #FFA500);
    color: #333;
    font-size: 1.5vw;
    box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.2);
    text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.3);
    transition: background 0.3s ease; /* добавлено для плавной смены цвета */
}

.custom-button:hover {
    background: linear-gradient(135deg, #FFA500, #FFD700); /* изменение градиента при наведении */
    cursor: pointer;
}

.custom-button span {
    font-weight: bold;
    letter-spacing: 1px;
}




@media screen and (min-width: 1700px){
    .logo1{
        position: relative;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        gap: 20px;
        width: 23%;
        height: 200px;
        background: rgba(255, 255, 255, 0.1);
        border-radius: 5px;
    
        img{
            position: absolute;
            width: 40%;
            height: auto;
            opacity: 0.2;
        }
    }
}
@media screen and (min-width: 1000px) {
    .logo:hover{
        position: relative;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        gap: 20px;
        width: 23%;
        height: 200px;
        background: rgba(255, 255, 255, 0.1);
        border-radius: 5px;
    
        img{
            position: absolute;
            width: 50%;
            height: auto;
            opacity: 0.2;
        }
        span{
            display: none;
        }
        .players_name{
            width: 90%;
            display: flex;
            align-items: center;
            flex-direction: column;
            color: white;
            gap: 5px;
            z-index: 1;
            div{
                display: flex;
                align-items: center;
                width: 65%;
                gap: 10px;
                img{
                    position: absolute;
                    width: 20px;
                    opacity: 1;
                }
                span{
                    display: inline;
                    font-size: 16px;
                    margin-left: 30px;
                    
                }
            }
            
        }
    }
}
@media screen and (max-width: 1000px) {
    .logo, .logo1{
        .players_name{
            div{
                span{
                    display: inline;
                    font-size: 12px;
                    margin-left: 30px;
                    
                }
            }
            
        }
    }
    
}
@media screen and (max-width: 900px){
    .show_players_div{

        left: 23%;
        span{
            font-size: 4vw;
            color: white;
            font-weight: bold;
    
        }   
    }
    .header{
        font-size: 10vw;
    }
}
@media (max-width: 800px) {
    .teamLogos {
        flex-wrap: wrap;
    }

    .teamLogos  {
        width: 95%;
    }

    .logo, .logo1{
        width: calc(50% - 10px);
        height: 200px;
    }

}

@media screen and (max-width: 600px){
    .header{
        font-size: 12vw;
    }
    .custom-button {
        font-size: 2.5vw;

    }
    
    
}