.wrapper_group_wrapper{
    display: flex;
    margin-left: auto;
    margin-right: auto;
    margin-top: 50px;
}
.group_wrapper{
    display: flex;
}
.group{
    display: flex;
    position: relative;
    flex-direction: column;
    justify-content: space-around;
    gap: 10px;
    margin-left: 20px;
}
.group span{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 150px;
    // width: 8vw;
    height: 40px;
    // height: 4vh;
    border: 1px solid #c1c153;
    background-color: rgba(255, 255, 255, 0.1);
    color: white;
    font-size: 16px;
    
}
.bracket_wrapper{
    display: flex;
    flex-direction: column;
    gap: 21px;
    box-sizing: border-box;
}
.bracket{
    display: flex;
    align-items: center;
    width: 20px;
    height: 6vh;
    border: 1px solid #c1c153;
    border-left: none;
    span{
        display: block;
        position: relative;
        left: 20px;
        border-top: 1px solid #c1c153;
        width: 20px;
    }
}
.bracket:first-child{
    position: relative;
    top: 5px;
    margin-bottom: 8px;
}
.bracket_wrapper1{
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    gap: 21px;
    box-sizing: border-box;
}
.bracket1{
    display: flex;
    align-items: center;
    width: 20px;
    height: 70px;
    border: 1px solid #c1c153;
    border-left: none;
    span{
        display: block;
        position: relative;
        left: 20px;
        border-top: 1px solid #c1c153;
        width: 20px;
    }
}
.bracket_wrapper2{
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: 21px;
    box-sizing: border-box;
}
.bracket2{
    display: flex;
    align-items: center;
    width: 20px;
    height: 130px;
    border: 1px solid #c1c153;
    border-left: none;
    span{
        display: block;
        position: relative;
        left: 20px;
        border-top: 1px solid #c1c153;
        width: 20px;
    }
}

.group_wrapper2{
    display: flex;
    flex-direction: row-reverse;
}

.reverse{
    transform: rotate(180deg);
    position: relative;
    left: 20px;
}
.winner{
    flex-shrink: 0;
    display: flex;
    position: relative;
    align-items: center;
    justify-content: center;
    color: white;
    width: 100px;
    height: 100px;
    font-size: 1vw;
    border: 1px solid #c1c153;
    background-color: rgba(255, 255, 255, 0.1);
    margin-top: auto;
    margin-bottom: auto;
    margin-left: 20px;
}

.winner div{
    position: absolute;
    left: 50%;
    bottom: -132px;
    height: 130px;
    border-left: 2px solid #c1c153;
}
.group div{
    position: absolute;
    left: 50%;
    bottom: -30px;
    height: 30px;
    border-left: 2px solid #c1c153;
}
.group_reverse{
    transform: rotate(180deg);
    
}
.group_reverse span{
    transform: rotate(180deg);

}

.reverse_winner{
    transform: rotate(180deg);
}
.reverse_winner span{
    transform: rotate(180deg);
}
.last_winner{
    display: flex;
    position: relative;
    align-items: center;
    justify-content: center;
    color: white;
    width: 100px;
    height: 100px;
    font-size: 1vw;
    border: 1px solid #c1c153;
    background-color: rgba(255, 255, 255, 0.1);
    margin-left: 20px;

}

.letters{
    font-size: 24px;
    color: white;
    margin-bottom: -30px;
    font-weight: bold;
    margin-left: 20px ;
}

