.tournamentsGrid_main{
    height: auto;
}
.match-info {
    background-color: rgba(255, 255, 255, 0.1);
    width: 60%;
    border-radius: 10px;
    padding: 20px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    color: white;
  }
  
.team{
    display: flex;
    width: 35%;
    justify-content: space-around;
}
  
  .team-name {
    font-weight: bold;
    font-size: 1vw;
  }
  
  .versus {
    font-size: 0.8vw;
  }
  
  .datetime {
    display: flex;
    justify-content: space-around;
    width: 20%;
    text-align: center;
    font-size: 1vw;
  }
  .status{
    display: flex;
    width: 30%;
    justify-content: space-around;
    align-items: center;
  }
  .status .pending{
    width: 1vw;
    height: 1vw;
    background-color: red;
    border-radius: 50%;
    animation: fadeOut 3s infinite;
    box-shadow: 0px 0px 10px 5px rgba(255, 0, 0, 0.5); /* красная тень */
  }
  .status .in_progress {
    width: 1vw;
    height: 1vw;
    background-color: green;
    border-radius: 50%;
    animation: fadeOut 3s infinite;
    box-shadow: 0px 0px 10px 5px rgba(0, 255, 0, 0.2); /* зеленая тень */
}
.status .completed {
    width: 1vw;
    height: 1vw;
    background-color: rgb(169, 169, 169);
    border-radius: 50%;
    
    box-shadow: 0px 0px 10px 5px rgba(0, 0, 0, 0.5); /* Темная тень */

}
  .status span{
    font-size: 1vw;
  }
  @keyframes fadeOut {
    0%, 100% {
        opacity: 1;
    }
    50% {
        opacity: 0;
    }
}

@media screen and (max-width:600px){
  .team-name {
    font-size: 2.5vw;
  }
  .versus {
    font-size: 2vw;
  }
  .datetime {

    font-size: 2.5vw;
  }
  .status span{
    font-size: 2.5vw;
  }
  .status .pending, .completed, .in_progress{
    width: 2vw;
    height: 2vw;
    margin-left: 10px;
  }
}
  

