
.stage_wrapper{
    display: flex;
    gap: 20px;
    width: auto;
    margin-left: auto;
    margin-right: auto;
}


.stage {
    position: relative; /* Установка позиционирования */
    display: flex;
    flex-direction: column;
    width: 188px;
    min-width: 188px;
    height: 313px;
    img {
        position: relative;
        width: 100%;
        height: 100%;
        object-fit: cover;
        opacity: 0.5;
        border-radius: 5px;
    }
    .stage_info{
        position: absolute;
        width: 100%;
        height: 100%;
        display: flex;
        align-items: center;
        flex-direction: column;
        font-weight: bold;
        .stage_date{
            width: 50%;
            height: 30px;
            border: 2px solid white;
            border-radius: 5px;
            display: flex;
            align-items: center;
            justify-content: center;
            color: white;
            margin-top: 10px;

        }
        span{
            color: white;
            font-size: 1.5rem;
        }
    }
}

.arrow_button{
    display: flex;
    align-items: center; 
    justify-content: center;
    position: relative;
    width: 40px;
    height: 40px;
    top: 10%;
    border-radius: 50%;
    background: #FEFF51;
    transition: transform 0.3s ease;
}
.arrow_button:hover{
    cursor: pointer;
    transform: scale(1.1);
}